.main-container {
    background-image: linear-gradient(to bottom right, #7A288A, #3498DB);
    background-size: 100% 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    position: relative;
  }
  
  .secret-button {
    position: absolute;
    top: 20px;
    right: 20px;
    padding: 10px 20px;
    border-radius: 15px;
    border: none;
    background-color: #fff;
    color: #333;
    cursor: pointer;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    font-size: 18px;
    transition: transform 0.2s ease-in-out;
  }
  
  .secret-button:hover {
    transform: scale(1.1);
  }
  
  .main-container h1 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .button-group {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 300px;
  }
  
  .main-button {
    padding: 15px 30px;
    border-radius: 15px;
    border: none;
    background-color: #fff;
    color: #333;
    cursor: pointer;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    font-size: 18px;
    transition: transform 0.2s ease-in-out;
  }
  
  .main-button:hover {
    transform: scale(1.05);
  }
  .main-container {
    background-image: linear-gradient(to bottom right, #7A288A, #3498DB);
    background-size: 100% 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    position: relative;
    padding: 20px;
    box-sizing: border-box;
  }
  
  .header-content {
    text-align: center;
    margin-bottom: 40px;
    max-width: 800px;
  }
  
  .header-content h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
  }
  
  .header-content h2 {
    font-size: 2rem;
    margin-bottom: 5px;
  }
  
  .age-info {
    font-size: 1.2rem;
    opacity: 0.9;
  }
  
  .skills-container {
    display: flex;
    justify-content: center;
    gap: 50px;
    margin-bottom: 40px;
    flex-wrap: wrap;
    max-width: 800px;
  }
  
  .skills-column {
    background: rgba(255, 255, 255, 0.1);
    padding: 20px 30px;
    border-radius: 15px;
    min-width: 250px;
  }
  
  .skills-column h3 {
    margin-top: 0;
    margin-bottom: 15px;
    font-size: 1.3rem;
    text-align: center;
  }
  
  .skills-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .skills-list li {
    padding: 8px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    font-size: 1.1rem;
  }
  
  .skills-list li:last-child {
    border-bottom: none;
  }
  
  .secret-button {
    position: absolute;
    top: 20px;
    right: 20px;
    padding: 10px 20px;
    border-radius: 15px;
    border: none;
    background-color: #fff;
    color: #333;
    cursor: pointer;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    font-size: 18px;
    transition: transform 0.2s ease-in-out;
  }
  
  .secret-button:hover {
    transform: scale(1.1);
  }
  
  .button-group {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 300px;
  }
  
  .main-button {
    padding: 15px 30px;
    border-radius: 15px;
    border: none;
    background-color: #fff;
    color: #333;
    cursor: pointer;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    font-size: 18px;
    transition: all 0.2s ease-in-out;
  }
  
  .main-button:hover {
    transform: scale(1.05);
    background-color: #f0f0f0;
  }
  
  @media (max-width: 768px) {
    .skills-container {
      flex-direction: column;
      gap: 20px;
    }
    
    .header-content h1 {
      font-size: 2rem;
    }
    
    .header-content h2 {
      font-size: 1.5rem;
    }
  }